.signup_container{
    display: grid;
    grid-template-columns: 1.1fr 0.9fr;
    height: 100vh;
    font-family: 'Roboto','sans-serif';
}

.right_signup{
    position: relative;
}

.left_signup{
    border: 1px solid #707070;
    background:linear-gradient(229deg,#A23F97,#612B7D,#612B7D);
    overflow: hidden;
}

.left_top{
        position: absolute;
        top: -120px;
        left: -160px;
}

.signup_content{
    display: flex;
    border: 1px solid black;
    justify-content: center;
    align-items: center;
    height: 100%;
    gap: 20px;
    flex-direction: column;
    margin: 50px auto;
}

.signup_heading{
    color: #FFFFFF;
    font-size: 40px;
    font-weight: 600;
    margin-left: -5%;
}

.signup_inputs{
    border-radius: 45px;
    height: 50px;
    width: 45%;
    background-color: transparent;
    border: 1.5px solid #FFFFFF; 
    padding-left: 14px;
    outline: none;
    margin-left: 20%;
}

.signup_inputs::placeholder{
    color: #F9EEFF;
}

.submit_details_btn{
    border-radius: 45px;
    height: 50px;
    width: 45%;
    background-color: #FFFFFF;
    border: 1px solid #FFFFFF; 
    padding-left: 14px;
    outline: none;
    margin-left: 20%;
    color: #612B7D;
    font-size: 25px;
    font-weight: 600 ;
}


.background{
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 40%;
    margin-top: 60px;
}

.right_content{
    display: flex;
    border: 1px solid black;
    margin-top:30% ;
    justify-content: center;
}

.signup_logo{
    justify-content: center;
    align-items: center;
    width: 40%;
}




